@import "../../index.scss";

.home {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 360px;
  min-height: 560px;
  height: 90vh;
  h1 {
    margin: 1.5rem 0 0.5rem 0;
    color: $green2;
    font-size: 2rem;
    font-weight: 900;
  }
  .home__products {
    width: 100%;
    margin: 1rem 0;
    h3 {
      letter-spacing: 0.1rem;
      padding: 0.1rem 0;
      color: white;
      text-align: center;
      background: $green2;
      font-weight: 500;
    }
    .home__product {
      position: relative;
      display: flex;
      overflow: hidden;
      height: 100%;
      background: rgba(233, 233, 233, 0.8);
    }
    .home__product:before {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      content: " ";
      background: url("../../img/logo.png");
      background-size: auto 90%;
      background-position: center;
      background-repeat: no-repeat;
      margin: auto;
      opacity: 0.15;
    }
  }
}
@media screen and(max-width:767px) {
  .home {
    overflow: scroll;
    position: relative;
    h1 {
      position: absolute;
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0 3.5rem);
      text-align: right;
      font-size: 1.8rem;
      margin: 0;
      padding: 0.5rem 0.7rem 1.8rem 0;
      width: 100%;
      background: rgb(255, 255, 255);
      overflow: hidden;
    }
  }
  .home > div:nth-child(2) {
    margin-top: auto;
    // border: 2px solid red;
  }
}
@media screen and(min-width:768px) {
  .home {
    position: relative;
    align-items: start;
    height: 85vh;
    min-height: 500px;
    overflow: hidden;
    h1 {
      margin-left: 1.4rem;
      padding-left: 0rem;
      text-shadow: 1px 3px 5px $green2;
      margin-bottom: 0;
    }
    .home__products {
      position: absolute;
      top: 1rem;
      right: 1.5rem;
      width: 20%;
      min-width: 16rem;
      height: 70vh;
      min-height: 400px;
      margin: auto;
      h3 {
        width: auto;
        border-radius: 0.3rem;
      }
      .home__product {
        display: block;
        margin: 0 0.3rem;
        border-radius: 0.3rem;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
      }
      .home__product:before {
        position: absolute;
        left: auto;
        top: 50%;
        transform: translateY(-50%);
        width: 100%;
        height: auto;
        background-size: 85% auto;
      }
    }
  }
}
@media screen and(min-width:2400px) {
  .home {
    height: 90vh;
    max-width: 2400px;
    margin: 0 auto;

    .home__products {
      top: 2rem;
      right: 2.5rem;
      width: 18vw;
      height: 75vh;
    }
    h3 {
      font-size: 1.5rem;
    }
  }
}
